.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: white;
  min-height: 2vh;
  display: flex;
  flex-direction:row;
  align-items: center;
  justify-content: left;
  font-size: calc(10px + 2vmin);
  color: rgb(216, 61, 61);
  border-bottom: 3px solid rgb(216, 61, 61);

}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App-body {
  padding-top: 30px;
  padding-left: 70px;
  padding-bottom: 380px;
  font-size: large;
  border-bottom: 3px solid rgb(216, 61, 61);
  font-weight: 400;
}

.App-footer {
  text-align: center;
  font-size: large;
  font-weight: 400;
}